//menu animation
.nav-toggle {
  z-index: 99;
  line-height: 1;
  display: inline-block;
  background-color: $mn-toggle;
  float: right;
  outline: none;
  padding: 0 vwSP(19);
  margin-left: auto;
  border: 0;
  .icon-menu {
    position: relative;
    display: inline-block;
    width: vwSP(50);
    height: vwSP(44);
    vertical-align: middle;
    fill: none;
    .line {
      position: absolute;
      left: 0;
      display: block;
      width: 100%;
      height: 2px;
      transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1.000);
      transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1.000);
      background-color: $line-color;
      border-radius: 0px;
    }
    .line-1 {
      top: 0;
    }
    .line-2 {
      top: 32%;

    }
    .line-4 {
      top: 64%;
    }
    .line-3 {
      bottom: 0;
    }
  }
}

nav.nav-container {
  position: absolute;
  top: 100%;
  width: 100%;
  height: 100vh;
  background-color: #707070;
  background-image: url("../img/Group 93.png");
  background-position: 50% 75%;
  background-repeat: no-repeat;
  @include translate(100%, 0);
  transition: all .3s;
  ul {
    width: 100%;
    background: #fff;
    padding-left: 0;
    li {
      display: block;
      text-align: left;
      + li {
        a {
          border-top: 2px solid #707070;
        }
      }
      a {
        display: block;
        color: #707070;
        font-size: vwSP(30);
        line-height: vwSP(32);
        padding: vwSP(39) vwSP(49) vwSP(29);
        padding-left: vwSP(45);
        &:after {
          font-family: "FontAwesome";
          content: "\f105";
          float: right;
          font-size: vwSP(50);
          line-height: .8;
          color: #707070;
        }
      }
    }
  }
}

.is-active {
  .icon-menu {
    .line-1 {
      transform: translateY(vwSP(21)) translateX(0) rotate(45deg);
    }

    .line-2, .line-4 {
      opacity: 0;
    }

    .line-3 {
      transform: translateY(vwSP(-21)) translateX(0) rotate(-45deg);
    }
  }
  .navbar nav.nav-container {
    display: block;
    @include translate(0, 0);
  }
}

