//pc
@include pc() {
  .navbar {
    z-index: 1;
    background: transparentize(#fff, .2);
    display: flex;
    align-items: center;
    nav.nav-container{
      position: static;
      width: initial;
      height: initial;
      background: none;
      transform: none;

    ul {
      background: none;
      text-align: center;
      font-size: 0;
      padding-left: vwPC(276);
      li {
        display: inline-block;
        font-size: 14px;
        a {
          font-size: vwPC(14);
          line-height: vwPC(15);
          color: #000;
          text-decoration: none;
          padding: 0 vwPC(14);
          border: none;
          &:hover {
            color: #9fb328;
          }
          &:after{
            content: none;
          }
        }
      }
    }
    }
  }

  .brand {
    padding: vwPC(22) vwPC(60) vwPC(18);
    padding-right: 0;
    h2, p {
      margin: 0;
      display: inline-block;
    }
    p {
      font-size: vwPC(11);
    }
    h2 {
      font-size: vwPC(20);
      padding-left: vwPC(11);
    }
    a {
      color: #000;
      text-decoration: none;
    }
  }

  .nav-toggle {
    display: none;
  }
  .bg-container{
    .japan-word{
      background-image: url(../img/japan-word-pc.png);
    }
  }
}